	module.exports = {"data":{"challengeNode":{"title":"Problem 395: Pythagorean tree","description":["The Pythagorean tree is a fractal generated by the following procedure:","","","","Start with a unit square. Then, calling one of the sides its base (in the animation, the bottom side is the base):"," Attach a right triangle to the side opposite the base, with the hypotenuse coinciding with that side and with the sides in a 3-4-5 ratio. Note that the smaller side of the triangle must be on the 'right' side with respect to the base (see animation)."," Attach a square to each leg of the right triangle, with one of its sides coinciding with that leg."," Repeat this procedure for both squares, considering as their bases the sides touching the triangle.","","The resulting figure, after an infinite number of iterations, is the Pythagorean tree.","","","","","","It can be shown that there exists at least one rectangle, whose sides are parallel to the largest square of the Pythagorean tree, which encloses the Pythagorean tree completely.","","","Find the smallest area possible for such a bounding rectangle, and give your answer rounded to 10 decimal places."],"challengeType":5,"fields":{"slug":"/coding-interview-prep/project-euler/problem-395-pythagorean-tree","blockName":"Project Euler","tests":[{"text":"<code>euler395()</code> should return 28.2453753155.","testString":"assert.strictEqual(euler395(), 28.2453753155, '<code>euler395()</code> should return 28.2453753155.');"}]},"required":[],"files":{"indexhtml":null,"indexjs":{"key":"indexjs","ext":"js","name":"index","contents":"function euler395() {\n  // Good luck!\n  return true;\n}\n\neuler395();","head":"","tail":""},"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/coding-interview-prep/project-euler/problem-396-weak-goodstein-sequence","id":"5900f4f71000cf542c51000a"},"slug":"/coding-interview-prep/project-euler/problem-395-pythagorean-tree"}}

